<template>
  <div class="love-oath">
    <div class="banner">
      <div id="scene">
        <div data-depth="0.00" class="layer"></div>
        <div data-depth="0.88" class="stars-cluster stars-cluster-1 layer">
          <img src="../../static/img/banner_icon03.png" class="banner-icon03">
        </div>
        <div data-depth="0.99" class="moon-wrapper layer">
          <img src="../../static/img/banner_icon02.png" class="banner-icon02">
        </div>
        <div data-depth="0.66" class="title-wrapper layer">
          <img src="../../static/img/banner_icon01.png" class="banner-icon01">
        </div>
        <div data-depth="0.77" class="title-wrapper layer">
          <div class="btn">
            <router-link to="/index"  class="btn-left">爱情誓言</router-link>
            <router-link to="/createLove"  class="btn-right">发布誓言</router-link>
          </div>
        </div>
      </div>
    </div>
    <!--/banner-->

    <div class="row my-oath">
      <h2>我的誓言</h2>
      <div class="list" v-for="love in loveList" :key="love.title">
        <div class="thumbnail">
          <img :src="love.image">
        </div>
        <div class="caption">
          <h3>{{love.title}}</h3>
          <p v-for="messagee in love.message.split('\n')" :key="messagee.message" style="height: 20px">
            {{messagee}}
          </p>
          <img src="../../static/img/comma01.png" class="comma_top">
          <img src="../../static/img/comma02.png" class="comma_bottom">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../libs/api'

export default {
  name: 'MyLove',
  data () {
    return {
      loveList: []
    }
  },
  created () {
    let self = this
    setTimeout(function () {
      api.getSelf(resp => {
        self.loveList = self.loveList.concat(JSON.parse(resp.result))
        self.loveList.reverse()
        setTimeout("window.scrollTo(0,800)",2000)
        }
      )
    },1000
    )
  }
}
</script>




